import * as Utils from './Utils';

export class Converter {
  public static bytesToBits(bytes: number): number {
    return bytes * 8;
  }
  public static bitsToBytes(bits: number): number {
    return Math.round(bits / 8);
  }
  public static bytesToKiloBytes(bytes: number): number {
    return Utils.calculateTime(bytes * 1000);
  }
  public static kiloBytesToMegaBytes(kiloByte: number): number {
    return kiloByte * 1000;
  }
}
